import { ValidationObserver } from 'vee-validate'
import countries from './data.json'
import Loader from '@/components/Loader'
export default {
  name: 'ContactSalesPage',
  components: {
    ValidationObserver,
    Loader
  },
  data () {
    return {
      form: {},
      loading: false,
      buttonText: 'Submit',
      countries,
      errorMsg: ''
    }
  },
  methods: {
    async onSubmit () {
      this.errorMsg = ''
      const self = this
      this.loading = true
      try {
        const { status, code } = await this.$axios.$post('api/v1/user/profile/contact-sales', this.form)
        if (status === 'success' && code === 200) {
          this.$refs.form.reset()
          this.form = {}
          this.buttonText = 'Message Sent'
        }
      } catch (error) {
        this.errorMsg = 'Unable to process request.'
        return error
      } finally {
        this.loading = false
        setTimeout(() => {
          self.buttonText = 'Submit'
        }, 3000)
      }
    }
  }
}
